import React, { useState } from 'react';
import { 
  Box, 
  TextField, 
  Button, 
  Typography, 
  useTheme, 
  Snackbar, 
  Alert, 
  CircularProgress, 
  Paper,
  Fade,
  useMediaQuery,
  alpha,
  styled
} from '@mui/material';
import { SvgIconComponent } from '@mui/icons-material';
import { motion } from 'framer-motion';
import { PersonalData } from '../data/data';
import Section from './Section';
import { sendEmail } from '../utility';

interface ContactInfoItem {
  title: string;
  text: string;
  icon: SvgIconComponent;
  href?: string;
}

const contactInfoData = PersonalData.PersonalProfile as unknown as ContactInfoItem[];

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  borderRadius: theme.shape.borderRadius * 2,
  background: theme.palette.mode === 'dark' 
    ? 'rgba(10, 25, 47, 0.3)' 
    : 'rgba(255, 255, 255, 0.7)',
  border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.1)' : 'rgba(25, 118, 210, 0.1)'}`,
  backdropFilter: 'blur(10px)',
  transition: 'all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1)',
  '&:hover': {
    transform: 'translateY(-4px)',
    boxShadow: theme.palette.mode === 'dark'
      ? '0 10px 30px -15px rgba(2, 12, 27, 0.7)'
      : '0 10px 30px -15px rgba(2, 12, 27, 0.2)',
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    borderRadius: 'inherit',
    padding: '1px',
    background: theme.palette.mode === 'dark'
      ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.2), rgba(0, 0, 0, 0))'
      : 'linear-gradient(135deg, rgba(25, 118, 210, 0.1), rgba(0, 0, 0, 0))',
    WebkitMask: 'linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)',
    WebkitMaskComposite: 'xor',
    pointerEvents: 'none',
  },
  '& .MuiInputBase-root': {
    borderRadius: '12px',
    backgroundColor: theme.palette.mode === 'dark' 
      ? 'rgba(255, 255, 255, 0.05)' 
      : 'rgba(25, 118, 210, 0.03)',
    '&:hover': {
      backgroundColor: theme.palette.mode === 'dark' 
        ? 'rgba(255, 255, 255, 0.08)' 
        : 'rgba(25, 118, 210, 0.05)',
    },
  },
  '& .MuiOutlinedInput-notchedOutline': {
    borderColor: theme.palette.mode === 'dark' 
      ? 'rgba(255, 255, 255, 0.1)' 
      : 'rgba(0, 0, 0, 0.1)',
  },
  '& .Mui-focused .MuiOutlinedInput-notchedOutline': {
    borderColor: theme.palette.mode === 'dark' 
      ? 'rgba(100, 255, 218, 0.8)' 
      : theme.palette.primary.main,
    boxShadow: theme.palette.mode === 'dark'
      ? '0 0 0 2px rgba(100, 255, 218, 0.2)'
      : '0 0 0 2px rgba(25, 118, 210, 0.1)',
  },
}));

const StyledTextField = styled(TextField)(({ theme }) => ({
  '& .MuiOutlinedInput-root': {
    backgroundColor: theme.palette.mode === 'dark' 
      ? 'rgba(255, 255, 255, 0.05)' 
      : 'rgba(25, 118, 210, 0.03)',
    '&:hover fieldset': {
      borderColor: theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.5)' : theme.palette.primary.main,
    },
    '&.Mui-focused fieldset': {
      borderColor: theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.8)' : theme.palette.primary.main,
      boxShadow: theme.palette.mode === 'dark' 
        ? '0 0 0 2px rgba(100, 255, 218, 0.2)' 
        : '0 0 0 2px rgba(25, 118, 210, 0.1)',
    },
    '&.Mui-focused': {
      backgroundColor: theme.palette.mode === 'dark' 
        ? 'rgba(100, 255, 218, 0.05)' 
        : 'rgba(25, 118, 210, 0.05)',
    },
  },
  '& .MuiInputLabel-root.Mui-focused': {
    color: theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.8)' : theme.palette.primary.main,
  },
}));

const SubmitButton = styled(Button)(({ theme }) => ({
  marginTop: theme.spacing(2),
  padding: theme.spacing(1.5, 4),
  borderRadius: '8px',
  textTransform: 'none',
  fontWeight: 600,
  letterSpacing: 0.5,
  background: theme.palette.mode === 'dark'
    ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.1), rgba(100, 255, 218, 0.2))'
    : 'linear-gradient(135deg, rgba(25, 118, 210, 0.1), rgba(25, 118, 210, 0.2))',
  border: theme.palette.mode === 'dark'
    ? '1px solid rgba(100, 255, 218, 0.3)'
    : '1px solid rgba(25, 118, 210, 0.3)',
  color: theme.palette.mode === 'dark' ? '#64ffda' : theme.palette.primary.main,
  transition: 'all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1)',
  '&:hover': {
    transform: 'translateY(-2px)',
    boxShadow: theme.palette.mode === 'dark'
      ? '0 5px 15px -5px rgba(100, 255, 218, 0.4)'
      : '0 5px 15px -5px rgba(25, 118, 210, 0.4)',
    background: theme.palette.mode === 'dark'
      ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.2), rgba(100, 255, 218, 0.3))'
      : 'linear-gradient(135deg, rgba(25, 118, 210, 0.2), rgba(25, 118, 210, 0.3))',
  },
  '&.Mui-disabled': {
    background: theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.05)' : 'rgba(25, 118, 210, 0.05)',
    color: theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.5)' : 'rgba(25, 118, 210, 0.5)',
  },
}));

const Contact: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });
  const [touched, setTouched] = useState({
    name: false,
    email: false,
    subject: false,
    message: false,
  });
  const [status, setStatus] = useState<{ type: 'idle' | 'loading' | 'success' | 'error'; message: string }>({
    type: 'idle',
    message: '',
  });

  const validateEmail = (email: string) => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };

  const validateField = (name: string, value: string) => {
    if (!value.trim()) return 'This field is required';
    if (name === 'email' && !validateEmail(value)) return 'Please enter a valid email';
    return '';
  };

  const getError = (name: string, value: string) => {
    return touched[name as keyof typeof touched] ? validateField(name, value) : '';
  };

  const isFormValid = () => {
    return (
      formData.name.trim() !== '' &&
      validateField('email', formData.email) === '' &&
      formData.subject.trim() !== '' &&
      formData.message.trim() !== ''
    );
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name } = e.target;
    setTouched(prev => ({
      ...prev,
      [name]: true,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setStatus({ type: 'loading', message: 'Sending message...' });
    try {
      const resp = await sendEmail(formData);
      setStatus({ type: 'success', message: 'Message sent successfully! I\'ll get back to you soon.' });
      setFormData({ name: '', email: '', subject: '', message: '' });
    } catch (error) {
      setStatus({ type: 'error', message: 'Failed to send message. Please try again later.' });
    }
  };

  const handleCloseSnackbar = () => {
    setStatus(prev => ({ ...prev, type: 'idle' }));
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.2,
      },
    },
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.5,
      },
    },
  };

  return (
    <Section id="contact" title="Get In Touch">
      <Box sx={{ display: 'flex', flexWrap: 'wrap', flexDirection:"column", gap: 4 }}>
        <Box>
          <motion.div
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.3 }}
            variants={containerVariants}
          >
            <Typography 
              variant="h4" 
              gutterBottom 
              sx={{ 
                fontWeight: 700, 
                mb: 4,
                background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
                display: 'inline-block',
              }}
            >
              Let's Connect
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 4, lineHeight: 1.8 }}>
              Have a project in mind or want to discuss potential opportunities? 
              Feel free to reach out through the contact form or connect with me on social media.
            </Typography>
            
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              {contactInfoData?.length && Array.isArray(contactInfoData) && contactInfoData.map((info: ContactInfoItem, index: number) => (
                <motion.div key={index} variants={itemVariants}>
                  <Box 
                    sx={{ 
                      display: 'flex', 
                      alignItems: 'flex-start', 
                      gap: 2,
                      p: 2,
                      borderRadius: 2,
                      transition: 'all 0.3s ease',
                      '&:hover': {
                        bgcolor: theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.05)' : 'rgba(0, 0, 0, 0.02)',
                        transform: 'translateY(-2px)',
                        boxShadow: theme.shadows[2],
                      },
                    }}
                    component={info.href ? 'a' : 'div'}
                    href={info.href}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <Box 
                      sx={{ 
                        width: 56, 
                        height: 56, 
                        borderRadius: '16px', 
                        bgcolor: alpha(theme.palette.primary.main, 0.1),
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        flexShrink: 0,
                        color: theme.palette.primary.main,
                        transition: 'all 0.3s ease',
                      }}
                    >
                      {React.createElement(info.icon, { 
                        'aria-hidden': true,
                        sx: { fontSize: 28 }
                      })}
                    </Box>
                    <Box>
                      <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 0.5 }}>
                        {info.title}
                      </Typography>
                      <Typography 
                        variant="body2" 
                        color={info.href ? 'primary' : 'text.secondary'}
                        sx={info.href ? { 
                          textDecoration: 'none',
                          '&:hover': {
                            textDecoration: 'underline',
                          },
                        } : {}}
                      >
                        {info.text}
                      </Typography>
                    </Box>
                  </Box>
                </motion.div>
              ))}
            </Box>
          </motion.div>
        </Box>

        <Box>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6, delay: 0.2 }}
          >
            <StyledPaper 
              elevation={0}
              sx={{ 
                p: { xs: 3, md: 5 },
                height: '100%',
                borderRadius: 4,
                position: 'relative',
                overflow: 'hidden',
              }}
            >
              <Typography 
                variant="h4" 
                sx={{ 
                  fontWeight: 700, 
                  mb: 4,
                  color: 'text.primary',
                }}
              >
                Send Me a Message
              </Typography>
              
              <form onSubmit={handleSubmit}>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 2, mb: 2 }}>
                  <Box sx={{ width: { xs: '100%', sm: 'calc(50% - 8px)' } }}>
                    <StyledTextField
                      label="Your Name"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      error={touched.name && !!getError('name', formData.name)}
                      helperText={touched.name && getError('name', formData.name)}
                      fullWidth
                      variant="outlined"
                      size="medium"
                    />
                  </Box>
                  
                  <Box sx={{ width: { xs: '100%', sm: 'calc(50% - 8px)' } }}>
                    <StyledTextField
                      label="Email Address"
                      name="email"
                      type="email"
                      value={formData.email}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      error={touched.email && !!getError('email', formData.email)}
                      helperText={touched.email && getError('email', formData.email)}
                      fullWidth
                      variant="outlined"
                      size="medium"
                    />
                  </Box>
                </Box>
                
                <Box sx={{ width: '100%', mb: 2 }}>
                  <StyledTextField
                    label="Subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.subject && !!getError('subject', formData.subject)}
                    helperText={touched.subject && getError('subject', formData.subject)}
                    fullWidth
                    variant="outlined"
                    size="medium"
                  />
                </Box>
                
                <Box sx={{ width: '100%', mb: 2 }}>
                  <StyledTextField
                    label="Your Message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.message && !!getError('message', formData.message)}
                    helperText={touched.message && getError('message', formData.message)}
                    fullWidth
                    multiline
                    rows={5}
                    variant="outlined"
                    size="medium"
                  />
                </Box>
                
                <Box sx={{ width: '100%', mb: 2 }}>
                  <motion.div
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                  >
                    <SubmitButton
                      type="submit"
                      variant="contained"
                      color="primary"
                      size="large"
                      disabled={status.type === 'loading' || !isFormValid()}
                      fullWidth
                    >
                      {status.type === 'loading' ? (
                        <CircularProgress size={24} color="inherit" />
                      ) : (
                        'Send Message'
                      )}
                    </SubmitButton>
                  </motion.div>
                </Box>
              </form>
            </StyledPaper>
          </motion.div>
        </Box>
      </Box>

      <Snackbar 
        open={status.type !== 'idle'} 
        autoHideDuration={6000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        TransitionComponent={Fade}
        sx={{
          '& .MuiPaper-root': {
            borderRadius: 2,
            boxShadow: theme.shadows[8],
          },
        }}
      >
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
        >
          <Alert 
            onClose={handleCloseSnackbar} 
            severity={status.type === 'success' ? 'success' : 'error'}
            variant="filled"
            sx={{
              borderRadius: 2,
              boxShadow: theme.shadows[4],
              '& .MuiAlert-icon': {
                fontSize: 28,
              },
              '& .MuiAlert-message': {
                fontSize: '1rem',
                fontWeight: 500,
              },
            }}
          >
            {status.message}
          </Alert>
        </motion.div>
      </Snackbar>
    </Section>
  );
};

export default Contact;
