import React from 'react';
import { CssBaseline, Box, Container } from '@mui/material';
import { ThemeProvider } from './theme';

// Components
import Header from './components/Header';
import Home from './components/Home';
import About from './components/About';
import Skills from './components/Skills';
import Experience from './components/Experience';
import Education from './components/Education';
import Projects from './components/Projects';
import Contact from './components/Contact';
import Footer from './components/Footer';



// Custom ScrollableAnchor component for smooth scrolling
const ScrollableAnchor: React.FC<{ id: string; children: React.ReactNode }> = ({ id, children }) => {
  return (
    <Box id={id} component="section" sx={{ scrollMarginTop: '80px' }}>
      {children}
    </Box>
  );
};

const App: React.FC = () => {

  return (
    <ThemeProvider>
      <CssBaseline />
      <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
        <Header 
          onNavClick={(section) => {
            const element = document.getElementById(section);
            if (element) {
              element.scrollIntoView({ behavior: 'smooth' });
            }
          }}
        />
        
        <Box component="main" sx={{ flexGrow: 1 }}>
          <Container maxWidth="lg" sx={{ py: 4 }}>
            <ScrollableAnchor id="home">
              <Home />
            </ScrollableAnchor>
            <ScrollableAnchor id="about">
              <About />
            </ScrollableAnchor>
            <ScrollableAnchor id="education">
              <Education />
            </ScrollableAnchor>
            <ScrollableAnchor id="experience">
              <Experience />
            </ScrollableAnchor>
            <ScrollableAnchor id="skills">
              <Skills />
            </ScrollableAnchor>
            <ScrollableAnchor id="projects">
              <Projects />
            </ScrollableAnchor>
            <ScrollableAnchor id="contact">
              <Contact />
            </ScrollableAnchor>
          </Container>
        </Box>
        <Footer />
      </Box>
    </ThemeProvider>
  );
};

export default App;