import React from 'react';
import { Box, Paper, Typography, useTheme } from '@mui/material';
import { styled } from '@mui/material/styles';
import { PersonalData } from '../data/data';
import Section from './Section';
import { Code, Storage, Dns, Cloud, Build, Security } from '@mui/icons-material';
import { SkillOptions } from '../data/types';
import {
  angularjsIcon,
  apacheIcon,
  awss3Icon,
  azuresqlIcon,
  codeIcon,
  cssIcon,
  dockerIcon,
  gitIcon,
  htmlIcon,
  ionicIcon,
  javaIcon,
  javascriptIcon,
  laravelIcon,
  minioIcon,
  mongodbIcon,
  mssqlIcon,
  mysqlIcon,
  nestjsIcon,
  nextjsIcon,
  nodejsIcon,
  phpIcon,
  reactjsIcon,
  springbootIcon,
  sqlIcon,
  sqliteIcon,
  tomcatIcon,
  typescriptIcon,
  ubuntuIcon,
  windowsIcon
} from '../assets/skill-icons';

// Map category names to icons
const categoryIcons: Record<string, React.ReactNode> = {
  'Frontend': <Code />,
  'Backend': <Storage />,
  'Databases': <Dns />,
  'DevOps': <Cloud />,
  'Tools': <Build />,
  'Security': <Security />
};

const skillIcons = (): Record<string, string> => {
  return {
    "html": htmlIcon,
    "css": cssIcon,
    "javascript": javascriptIcon,
    "angularjs": angularjsIcon,
    "reactjs": reactjsIcon,
    "ionic": ionicIcon,
    "sql": sqlIcon,
    "sqlite": sqliteIcon,
    "mysql": mysqlIcon,
    "mssql": mssqlIcon,
    "azuresql": azuresqlIcon,
    "mongodb": mongodbIcon,
    "php": phpIcon,
    "laravel": laravelIcon,
    "nodejs": nodejsIcon,
    "nestjs": nestjsIcon,
    "java": javaIcon,
    "docker": dockerIcon,
    "windows": windowsIcon,
    "ubuntu": ubuntuIcon,
    "apache": apacheIcon,
    "minio": minioIcon,
    "git": gitIcon,
    "aws-s3": awss3Icon,
    "code": codeIcon,
    "typescript": typescriptIcon,
    "springboot": springbootIcon,
    "tomcat": tomcatIcon,
    "nextjs": nextjsIcon
  };
};

const StyledImage = styled('img')(({ theme }) => ({
  objectFit: 'contain',
  display: 'block',
  margin: '0 auto',
  transition: "scale 0.5s",
  "&:hover": {
    scale: 2
  }
}));

const skillIcon = (skill: string, size: number = 48) => {
  const icons = skillIcons();
  const src = icons[skill] || codeIcon;
  return <StyledImage
    src={src}
    alt={skill}
    width={size}
    height={size}
  />;
};


const SkillCard = styled(Paper)(({ theme }) => ({
  padding: '16px 20px',
  borderRadius: '8px',
  transition: 'all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1)',
  background: theme.palette.mode === 'dark'
    ? 'rgba(10, 25, 47, 0.3)'
    : 'rgba(255, 255, 255, 0.7)',
  border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.1)' : 'rgba(25, 118, 210, 0.1)'}`,
  backdropFilter: 'blur(10px)',
  '&:hover': {
    transform: 'translateY(-4px)',
    boxShadow: theme.palette.mode === 'dark'
      ? '0 10px 30px -15px rgba(2, 12, 27, 0.7)'
      : '0 10px 30px -15px rgba(2, 12, 27, 0.2)',
  },
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    borderRadius: 'inherit',
    padding: '1px',
    background: theme.palette.mode === 'dark'
      ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.2), rgba(0, 0, 0, 0))'
      : 'linear-gradient(135deg, rgba(25, 118, 210, 0.1), rgba(0, 0, 0, 0))',
    WebkitMask: 'linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)',
    WebkitMaskComposite: 'xor',
    pointerEvents: 'none',
  },
}));

const Skills: React.FC = () => {
  const theme = useTheme();
  const { CategoriesSkills } = PersonalData;

  return (
    <Section id="skills" title="Skills">
      <Box
        sx={{
          display: 'grid',
          gridTemplateColumns: {
            xs: '1fr',
            sm: 'repeat(auto-fill, minmax(400px, 1fr))',
          },
          gap: 5,
          mt: 1,
        }}
      >
        {Object.entries(CategoriesSkills).map(([category, skills]) => (
          <SkillCard key={category} sx={{ width: '100%' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <Box
                sx={{
                  width: 36,
                  height: 36,
                  borderRadius: 1,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  mr: 1.5,
                  color: theme.palette.primary.main,
                  backgroundColor: theme.palette.mode === 'dark'
                    ? 'rgba(144, 202, 249, 0.1)'
                    : 'rgba(25, 118, 210, 0.08)',
                }}
              >
                {categoryIcons[category] || <Code />}
              </Box>
              <Typography variant="h6" component="h3">
                {category}
              </Typography>
            </Box>
            <Box
              sx={{
                display: 'grid',
                gridTemplateColumns: {
                  xs: '1fr 1fr 1fr',
                  sm: '1fr 1fr 1fr',
                  md: '1fr 1fr 1fr',
                },
                gap: 2,
              }}
            >
              {(skills as SkillOptions[]).map((skill) => {
                const percentage = skill.respondents ? Math.round(skill.respondents) : null;
                return (
                  <Box
                    key={skill.name}
                    sx={{
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'center',
                      justifyContent: 'center',
                      py: 2
                    }}
                  >
                    <Box sx={{ position: 'relative', width: 100, height: 100, mb: 1 }}>
                      <Box
                        sx={{
                          position: 'absolute',
                          top: 0,
                          left: 0,
                          width: '100%',
                          height: '100%',
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          flexDirection: 'column',
                          border: `1px solid ${theme.palette.primary.main}`,
                          borderRadius: '25px',
                          boxShadow: `0 0 15px 0 ${theme.palette.primary.main}`,
                        }}
                      >
                        {skillIcon(skill.icon || 'code', 60)}
                      </Box>
                    </Box>
                    <Typography
                      variant="subtitle2"
                      align="center"
                      sx={{ mt: 1, fontWeight: 500 }}
                    >
                      {skill.name}
                    </Typography>
                  </Box>
                );
              })}
            </Box>
          </SkillCard>
        ))}
      </Box>
    </Section>
  );
};

export default Skills;
