import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  Paper, 
  Chip, 
  useTheme, 
  Button, 
  Tooltip,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Divider,
  List,
  ListItem,
  ListItemText,
  Container,
  Grid
} from '@mui/material';
import { PersonalData } from '../data/data';
import Section from './Section';
import { ProjectHistoryOptions } from '../data/types';

// Icons
import GitHubIcon from '@mui/icons-material/GitHub';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import CodeIcon from '@mui/icons-material/Code';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';

// Styled Components
const ProjectCard = styled(Paper)(({ theme }) => ({
  height: '100%',
  display: 'flex',
  flexDirection: 'column',
  borderRadius: 12,
  overflow: 'hidden',
  transition: 'all 0.3s ease-in-out',
  background: theme.palette.mode === 'dark' 
    ? 'rgba(30, 30, 47, 0.6)' 
    : 'rgba(255, 255, 255, 0.8)',
  backdropFilter: 'blur(10px)',
  border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.05)'}`,
  '&:hover': {
    transform: 'translateY(-8px)',
    boxShadow: theme.shadows[8],
    '& .project-image': {
      transform: 'scale(1.05)',
    },
    '& .view-details': {
      opacity: 1,
      transform: 'translateY(0)',
    }
  },
}));

const ProjectImage = styled('div')(({ theme }) => ({
  position: 'relative',
  height: 180,
  overflow: 'hidden',
  '&::before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'linear-gradient(0deg, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 50%)',
    zIndex: 1,
  },
  '& img': {
    width: '100%',
    height: '100%',
    objectFit: 'cover',
    transition: 'transform 0.5s ease',
  },
}));

const TechChip = styled(Chip)(({ theme }) => ({
  margin: theme.spacing(0.5),
  backgroundColor: theme.palette.mode === 'dark' 
    ? 'rgba(91, 173, 254, 0.15)' 
    : 'rgba(25, 118, 210, 0.1)',
  color: theme.palette.mode === 'dark' ? '#5badfe' : theme.palette.primary.main,
  fontWeight: 500,
  fontSize: '0.7rem',
  height: 24,
  '& .MuiChip-label': {
    padding: '0 8px',
  },
}));

// Prepare projects data
const projectsData = PersonalData.ProjectHistory.map((project, index) => ({
  id: index + 1,
  title: project.name.replace('➢', '').trim(),
  description: project.description[0] || 'No description available',
  fullDescription: project.description.join(' '),
  image: '',
  technologies: [
    ...(project.keyTechnologies.frontend || []),
    ...(project.keyTechnologies.backend || []),
    ...(project.keyTechnologies.databases || []),
    ...(project.keyTechnologies.cloudServer || [])
  ],
  features: project.description,
  featured: true,
  demoUrl: '#',
  githubUrl: '#'
}));

interface Project {
  id: number;
  title: string;
  description: string;
  fullDescription: string;
  image: string;
  technologies: string[];
  features?: string[];
  featured?: boolean;
  demoUrl?: string;
  githubUrl?: string;
}

interface ProjectDialogProps {
  open: boolean;
  onClose: () => void;
  project: Project | null;
}

// Project Dialog Component
const ProjectDialog: React.FC<ProjectDialogProps> = ({ 
  open, 
  onClose, 
  project 
}) => {
  const theme = useTheme();
  
  if (!project) return null;

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="md"
      fullWidth
      PaperProps={{
        sx: {
          borderRadius: 3,
          background: theme.palette.mode === 'dark' 
            ? 'rgba(30, 30, 47, 0.95)' 
            : 'rgba(255, 255, 255, 0.98)',
          backdropFilter: 'blur(20px)',
          border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.1)' : 'rgba(0, 0, 0, 0.1)'}`,
        },
      }}
    >
      <DialogTitle sx={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        pb: 1,
        borderBottom: `1px solid ${theme.palette.divider}`
      }}>
        <Typography variant="h5" component="div" sx={{ fontWeight: 700 }}>
          {project.title}
        </Typography>
        <IconButton onClick={onClose} size="small">
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      
      <DialogContent sx={{ pt: 3, pb: 1 }}>
        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: 'repeat(2, 1fr)' }, gap: 4 }}>
          <Box>
            <Box 
              sx={{ 
                borderRadius: 2, 
                overflow: 'hidden',
                boxShadow: theme.shadows[2],
                height: '100%',
                minHeight: 250,
                background: theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.05)' : 'rgba(0, 0, 0, 0.02)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              <CodeIcon sx={{ fontSize: 60, opacity: 0.2 }} />
            </Box>
          </Box>
          
          <Box>
            <Box sx={{ mb: 3 }}>
              <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 1 }}>
                Project Overview
              </Typography>
              <Typography variant="body1" color="text.secondary">
                {project.fullDescription}
              </Typography>
            </Box>
            
            <Box sx={{ mb: 3 }}>
              <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 1 }}>
                Key Features
              </Typography>
              <List dense disablePadding>
                {project.features?.map((feature, index) => (
                  <ListItem 
                    key={index} 
                    disableGutters 
                    sx={{ py: 0.5, alignItems: 'flex-start' }}
                  >
                    <Box component="span" sx={{ color: 'primary.main', mr: 1 }}>•</Box>
                    <ListItemText 
                      primary={
                        <Typography variant="body2" color="text.secondary">
                          {feature}
                        </Typography>
                      } 
                    />
                  </ListItem>
                ))}
              </List>
            </Box>
            
            <Box>
              <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 1 }}>
                Technologies Used
              </Typography>
              <Box sx={{ display: 'flex', flexWrap: 'wrap', mt: 1 }}>
                {project.technologies.map((tech, index) => (
                  <TechChip 
                    key={index} 
                    label={tech} 
                    size="small"
                    sx={{ mb: 1, mr: 1 }}
                  />
                ))}
              </Box>
            </Box>
          </Box>
        </Box>
      </DialogContent>
      
      <DialogActions sx={{ px: 3, py: 2, borderTop: `1px solid ${theme.palette.divider}` }}>
        <Button 
          startIcon={<GitHubIcon />} 
          component="a"
          href={project.githubUrl || '#'}
          target="_blank"
          rel="noopener noreferrer"
          disabled={!project.githubUrl || project.githubUrl === '#'}
          sx={{ textTransform: 'none' }}
        >
          View Code
        </Button>
        <Button 
          variant="contained" 
          color="primary" 
          component="a"
          endIcon={<OpenInNewIcon />}
          href={project.demoUrl || '#'}
          target="_blank"
          rel="noopener noreferrer"
          disabled={!project.demoUrl || project.demoUrl === '#'}
          sx={{ textTransform: 'none' }}
        >
          Live Demo
        </Button>
      </DialogActions>
    </Dialog>
  );
};

const Projects: React.FC = () => {
  const theme = useTheme();
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  const [dialogOpen, setDialogOpen] = useState(false);

  const handleProjectClick = (project: Project) => {
    setSelectedProject(project);
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setTimeout(() => setSelectedProject(null), 300);
  };

  return (
    <Section id="projects" title="My Projects" subtitle="Some of my recent work">
      <Container maxWidth="lg">
        <Box sx={{ 
          display: 'grid', 
          gridTemplateColumns: { 
            xs: '1fr', 
            sm: 'repeat(2, 1fr)', 
            lg: 'repeat(3, 1fr)' 
          }, 
          gap: 3 
        }}>
          {projectsData
            .filter((project: Project) => project.featured)
            .map((project: Project) => (
              <Box key={project.id}>
                <ProjectCard 
                  elevation={0}
                  onClick={() => handleProjectClick(project)}
                  sx={{ cursor: 'pointer' }}
                >
                  <ProjectImage className="project-image">
                    <Box 
                      sx={{ 
                        width: '100%', 
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        background: theme.palette.mode === 'dark' 
                          ? 'linear-gradient(45deg, #1a1a2e 0%, #16213e 100%)' 
                          : 'linear-gradient(45deg, #f5f7fa 0%, #e9f0f9 100%)',
                      }}
                    >
                      <CodeIcon sx={{ 
                        fontSize: 60, 
                        color: theme.palette.mode === 'dark' 
                          ? 'rgba(255, 255, 255, 0.1)' 
                          : 'rgba(0, 0, 0, 0.08)' 
                      }} 
                      />
                    </Box>
                    <Box 
                      className="view-details"
                      sx={{
                        position: 'absolute',
                        bottom: 0,
                        left: 0,
                        right: 0,
                        p: 2,
                        background: 'linear-gradient(0deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 100%)',
                        color: 'white',
                        zIndex: 2,
                        opacity: 0,
                        transform: 'translateY(10px)',
                        transition: 'all 0.3s ease',
                      }}
                    >
                      <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                        View Project Details
                      </Typography>
                    </Box>
                  </ProjectImage>
                  
                  <Box sx={{ p: 3, flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
                    <Typography 
                      variant="h6" 
                      component="h3" 
                      sx={{ 
                        fontWeight: 700, 
                        mb: 1.5,
                        background: theme.palette.mode === 'dark'
                          ? 'linear-gradient(45deg, #5badfe, #6c63ff)'
                          : 'linear-gradient(45deg, #1976d2, #5c6bc0)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent',
                        display: 'inline-block',
                      }}
                    >
                      {project.title}
                    </Typography>
                    
                    <Typography 
                      variant="body2" 
                      color="text.secondary" 
                      sx={{ 
                        mb: 2.5,
                        display: '-webkit-box',
                        WebkitLineClamp: 3,
                        WebkitBoxOrient: 'vertical',
                        overflow: 'hidden',
                      }}
                    >
                      {project.description}
                    </Typography>
                    
                    <Box sx={{ mt: 'auto', pt: 2, borderTop: `1px solid ${theme.palette.divider}` }}>
                      <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                        {project.technologies.slice(0, 4).map((tech, index) => (
                          <TechChip 
                            key={index} 
                            label={tech} 
                            size="small"
                          />
                        ))}
                        {project.technologies.length > 4 && (
                          <Tooltip 
                            title={
                              <Box>
                                {project.technologies.slice(4).map((tech, i) => (
                                  <Box key={i} sx={{ py: 0.5 }}>{tech}</Box>
                                ))}
                              </Box>
                            }
                            arrow
                          >
                            <Chip 
                              label={`+${project.technologies.length - 4}`} 
                              size="small"
                              sx={{ 
                                backgroundColor: 'transparent',
                                color: 'text.secondary',
                                border: `1px solid ${theme.palette.divider}`,
                                height: 24,
                                '& .MuiChip-label': { px: 1 },
                              }}
                            />
                          </Tooltip>
                        )}
                      </Box>
                    </Box>
                  </Box>
                </ProjectCard>
              </Box>
            ))}
        </Box>
      </Container>
      
      <ProjectDialog 
        open={dialogOpen} 
        onClose={handleCloseDialog} 
        project={selectedProject} 
      />
    </Section>
  );
};

export default Projects;
