import React from 'react';
import { Box, Typography, Button, Container, useTheme } from '@mui/material';
import { PersonalData } from '../data/data';

// Tech circuit board background with floating elements
const TechBackground: React.FC = () => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';
  
  // Generate floating tech elements
  const techElements = Array.from({ length: 12 }).map((_, i) => ({
    id: i,
    content: Math.random() > 0.5 ? '{ }' : Math.random().toString(2).slice(2, 6),
    size: Math.random() * 1 + 0.8,
    x: Math.random() * 100,
    y: Math.random() * 100,
    duration: Math.random() * 30 + 30,
    delay: Math.random() * 10,
    opacity: Math.random() * 0.3 + 0.1,
  }));

  // Circuit board grid pattern
  const gridPattern = `
    <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <pattern id="grid" width="40" height="40" patternUnits="userSpaceOnUse">
          <path d="M 40 0 L 0 0 0 40" fill="none" stroke="${isDark ? 'rgba(144, 202, 249, 0.1)' : 'rgba(25, 118, 210, 0.1)'}" stroke-width="1"/>
        </pattern>
        <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="100%">
          <stop offset="0%" stop-color="${isDark ? '#5badfe' : '#1976d2'}" />
          <stop offset="100%" stop-color="${isDark ? '#6c63ff' : '#5c6bc0'}" />
        </linearGradient>
      </defs>
      <rect width="100%" height="100%" fill="url(#grid)" />
    </svg>
  `;

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        zIndex: -1,
        overflow: 'hidden',
        pointerEvents: 'none',
        background: isDark 
          ? 'linear-gradient(135deg, #0a192f 0%, #112240 100%)' 
          : 'linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%)',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: `url("data:image/svg+xml,${encodeURIComponent(gridPattern)}")`,
          opacity: 0.5,
          animation: 'gridMove 100s linear infinite',
        },
        '@keyframes gridMove': {
          '0%': { backgroundPosition: '0 0' },
          '100%': { backgroundPosition: '1000px 1000px' },
        },
      }}
    >
      {/* Circuit nodes */}
      {Array.from({ length: 20 }).map((_, i) => {
        const size = Math.random() * 6 + 2;
        return (
          <Box
            key={`node-${i}`}
            sx={{
              position: 'absolute',
              width: size,
              height: size,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              borderRadius: '50%',
              background: 'url(#gradient)',
              opacity: 0.6,
              filter: 'blur(1px)',
            }}
          />
        );
      })}

      {/* Floating tech elements */}
      {techElements.map((el) => (
        <Box
          key={`tech-${el.id}`}
          sx={{
            position: 'absolute',
            left: `${el.x}%`,
            top: `${el.y}%`,
            fontSize: `${el.size}rem`,
            fontFamily: 'monospace',
            color: isDark ? 'rgba(144, 202, 249, 0.3)' : 'rgba(25, 118, 210, 0.2)',
            opacity: el.opacity,
            animation: `techFloat ${el.duration}s ease-in-out ${el.delay}s infinite`,
            '@keyframes techFloat': {
              '0%, 100%': { 
                transform: 'translate(0, 0) rotate(0deg)',
                opacity: el.opacity,
              },
              '50%': { 
                transform: `translate(${Math.random() * 40 - 20}px, ${Math.random() * 40 - 20}px) rotate(${Math.random() * 360}deg)`,
                opacity: el.opacity * 1.5,
              },
            },
          }}
        >
          {el.content}
        </Box>
      ))}

      {/* Animated circuit paths */}
      <Box
        sx={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: `
            radial-gradient(circle at 20% 30%, ${isDark ? 'rgba(91, 173, 254, 0.1)' : 'rgba(25, 118, 210, 0.05)'} 0%, transparent 25%),
            radial-gradient(circle at 80% 70%, ${isDark ? 'rgba(108, 99, 255, 0.1)' : 'rgba(92, 107, 192, 0.05)'} 0%, transparent 25%)
          `,
          '&::before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: `url("data:image/svg+xml,%3Csvg width='100' height='20' viewBox='0 0 100 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 10 C 20 0, 40 20, 60 10 S 100 0, 100 10' fill='none' stroke='${isDark ? 'rgba(91, 173, 254, 0.05)' : 'rgba(25, 118, 210, 0.03)'}' stroke-width='1' /%3E%3C/svg%3E")`,
            opacity: 0.5,
            animation: 'circuitMove 20s linear infinite',
          },
          '@keyframes circuitMove': {
            '0%': { backgroundPosition: '0 0' },
            '100%': { backgroundPosition: '100px 100px' },
          },
        }}
      />
    </Box>
  );
};

const Home: React.FC = () => {
  const theme = useTheme();
  
  return (
    <Box
      component="section"
      id="home"
      sx={{
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        textAlign: 'center',
        position: 'relative',
        overflow: 'hidden',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: (theme) => theme.palette.mode === 'dark'
            ? 'linear-gradient(180deg, rgba(18,18,18,0.9) 0%, rgba(30,30,47,0.8) 100%)'
            : 'linear-gradient(180deg, rgba(248,249,250,0.9) 0%, rgba(233,236,239,0.8) 100%)',
          backdropFilter: 'blur(4px)',
          zIndex: -1,
          pointerEvents: 'none',
        },
      }}
    >
      <TechBackground />
      <Container 
        maxWidth="md" 
        sx={{ 
          position: 'relative', 
          zIndex: 1,
          backdropFilter: 'blur(10px)',
          backgroundColor: theme.palette.mode === 'dark' 
            ? 'rgba(10, 25, 47, 0.7)' 
            : 'rgba(255, 255, 255, 0.8)',
          borderRadius: '16px',
          border: `1px solid ${theme.palette.mode === 'dark' ? 'rgba(100, 255, 218, 0.1)' : 'rgba(25, 118, 210, 0.2)'}`,
          boxShadow: theme.palette.mode === 'dark'
            ? '0 10px 30px -15px rgba(2, 12, 27, 0.7)'
            : '0 10px 30px -15px rgba(2, 12, 27, 0.2)',
          padding: { xs: 3, md: 6 },
          marginTop: { xs: 4, md: 0 },
          marginBottom: { xs: 4, md: 0 },
          transition: 'all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1)',
          '&::before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            borderRadius: 'inherit',
            padding: '1px',
            background: theme.palette.mode === 'dark'
              ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.3), rgba(0, 0, 0, 0))'
              : 'linear-gradient(135deg, rgba(25, 118, 210, 0.3), rgba(0, 0, 0, 0))',
            WebkitMask: 'linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)',
            WebkitMaskComposite: 'xor',
            pointerEvents: 'none',
          },
          '&:hover': {
            transform: 'translateY(-5px)',
            '&::before': {
              background: theme.palette.mode === 'dark'
                ? 'linear-gradient(135deg, rgba(100, 255, 218, 0.5), rgba(0, 0, 0, 0))'
                : 'linear-gradient(135deg, rgba(25, 118, 210, 0.5), rgba(0, 0, 0, 0))',
            },
          },
        }}
      >
        <Box
          sx={{
            position: 'relative',
            '&::before': {
              content: '""',
              position: 'absolute',
              width: '200px',
              height: '200px',
              borderRadius: '50%',
              background: theme.palette.mode === 'dark' 
                ? 'linear-gradient(45deg, #5badfe, #6c63ff)'
                : 'linear-gradient(45deg, #1976d2, #5c6bc0)',
              filter: 'blur(80px)',
              top: '-100px',
              right: '-50px',
              zIndex: -1,
              opacity: theme.palette.mode === 'dark' ? 0.3 : 0.2,
              '@keyframes float': {
                '0%, 100%': { transform: 'translateY(0)' },
                '50%': { transform: 'translateY(-20px)' },
              },
              animation: 'float 6s ease-in-out infinite',
            },
          }}
        >
          <Typography
            variant="h2"
            component="h1"
            gutterBottom
            sx={{
              fontWeight: 800,
              mb: 3,
              background: theme.palette.mode === 'dark'
                ? 'linear-gradient(45deg, #5badfe, #6c63ff)'
                : 'linear-gradient(45deg, #1976d2, #5c6bc0)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              display: 'inline-block',
              position: 'relative',
              transition: 'all 0.3s cubic-bezier(0.25, 0.8, 0.5, 1)',
              '&:hover': {
                transform: 'translateY(-2px)',
                textShadow: theme.palette.mode === 'dark'
                  ? '0 5px 15px rgba(91, 173, 254, 0.4)'
                  : '0 5px 15px rgba(25, 118, 210, 0.2)',
              },
              '&::after': {
                content: '""',
                position: 'absolute',
                bottom: 0,
                left: 0,
                width: '100%',
                height: '3px',
                background: theme.palette.mode === 'dark'
                  ? 'linear-gradient(90deg, #5badfe, #6c63ff)'
                  : 'linear-gradient(90deg, #1976d2, #5c6bc0)',
                transform: 'scaleX(0)',
                transformOrigin: 'right',
                transition: 'transform 0.5s cubic-bezier(0.22, 1, 0.36, 1)',
                borderRadius: '2px',
              },
              '&:hover::after': {
                transform: 'scaleX(1)',
                transformOrigin: 'left',
              },
            }}
          >
            Hi, I'm {PersonalData.PersonalProfile.name}
          </Typography>
        </Box>
        
        <Typography
          variant="h4"
          component="h2"
          sx={{
            mb: 4,
            color: 'text.primary',
            fontWeight: 600,
            position: 'relative',
            display: 'inline-block',
            '&::after': {
              content: '""',
              position: 'absolute',
              bottom: '-8px',
              left: '50%',
              transform: 'translateX(-50%)',
              width: '50px',
              height: '3px',
              background: theme.palette.primary.main,
              borderRadius: '2px',
            },
          }}
        >
          {PersonalData.PersonalProfile.title}
        </Typography>
        
        <Typography
          variant="h6"
          sx={{
            maxWidth: "-webkit-fill-available",
            mx: 'auto',
            mb: 6,
            color: theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.8)' : 'rgba(0, 0, 0, 0.7)',
            lineHeight: 1.8,
            fontSize: '1.1rem',
            position: 'relative',
            textAlign: 'center'
          }}
        >
          {PersonalData.PersonalProfile.aboutMe}
        </Typography>
        
        <Box sx={{ '& > *:not(:last-child)': { mr: 2 }, mt: 4 }}>
          <Button
            variant="contained"
            size="large"
            sx={{
              background: theme.palette.mode === 'dark'
                ? 'linear-gradient(45deg, #5badfe, #6c63ff)'
                : 'linear-gradient(45deg, #1976d2, #303f9f)',
              color: 'white',
              fontWeight: 600,
              px: 4,
              py: 1.5,
              borderRadius: '8px',
              boxShadow: '0 4px 15px rgba(91, 173, 254, 0.3)',
              transition: 'all 0.3s ease',
              '&:hover': {
                transform: 'translateY(-2px)',
                boxShadow: '0 6px 20px rgba(91, 173, 254, 0.4)',
              },
              '&:active': {
                transform: 'translateY(0)',
              },
            }}
            onClick={() => {
              document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' });
            }}
          >
            Contact Me
          </Button>
          <Button
            variant="outlined"
            size="large"
            sx={{
              ml: 3,
              px: 4,
              py: 1.5,
              fontWeight: 600,
              borderRadius: '8px',
              borderWidth: '2px',
              borderColor: theme.palette.mode === 'dark' ? '#5badfe' : '#1976d2',
              color: theme.palette.mode === 'dark' ? '#5badfe' : '#1976d2',
              transition: 'all 0.3s ease',
              '&:hover': {
                borderWidth: '2px',
                backgroundColor: theme.palette.mode === 'dark' 
                  ? 'rgba(91, 173, 254, 0.1)' 
                  : 'rgba(25, 118, 210, 0.1)',
                transform: 'translateY(-2px)',
              },
              '&:active': {
                transform: 'translateY(0)',
              },
            }}
            onClick={() => {
              document.getElementById('about')?.scrollIntoView({ behavior: 'smooth' });
            }}
          >
            Learn More
          </Button>
        </Box>
      </Container>
    </Box>
  );
};

export default Home;
