import React, { ReactNode } from 'react';
import { Box, BoxProps, Typography, useTheme, SxProps, Theme } from '@mui/material';

interface SectionProps extends BoxProps {
  id: string;
  title: string;
  subtitle?: string;
  children: ReactNode;
  sx?: SxProps<Theme>;
}

const Section: React.FC<SectionProps> = ({ 
  id, 
  title, 
  subtitle, 
  children, 
  sx = {},
  ...otherProps 
}) => {
  const theme = useTheme();
  
  return (
    <Box 
      id={id} 
      component="section" 
      sx={[{
        py: { xs: 6, md: 10 }, 
        scrollMarginTop: '80px' 
      }, ...(Array.isArray(sx) ? sx : [sx])]}
      {...otherProps}
    >
      <Box sx={{ mb: 6, textAlign: 'center' }}>
        <Typography 
          variant="h3" 
          component="h2" 
          sx={{ 
            fontWeight: 800,
            mb: 1,
            position: 'relative',
            display: 'inline-block',
            '&:after': {
              content: '""',
              position: 'absolute',
              bottom: -8,
              left: '50%',
              transform: 'translateX(-50%)',
              width: '80px',
              height: '4px',
              background: theme.palette.mode === 'dark'
                ? 'linear-gradient(45deg, #5badfe, #6c63ff)'
                : 'linear-gradient(45deg, #1976d2, #5c6bc0)',
              borderRadius: '2px',
            },
          }}
        >
          {title}
        </Typography>
        {subtitle && (
          <Typography 
            variant="subtitle1" 
            sx={{ 
              color: 'text.secondary',
              mt: 2,
              fontSize: '1.1rem',
            }}
          >
            {subtitle}
          </Typography>
        )}
      </Box>
      {children}
    </Box>
  );
};

export default Section;
