import React, { MouseEvent } from 'react';
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  IconButton, 
  useTheme,
  Box,
  Container,
  Stack,
  Tooltip,
  styled,
  SvgIcon,
  SvgIconProps
} from '@mui/material';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { useThemeContext } from '../theme';
import { PersonalData } from '../data/data';
import { LinkedIn, GitHub, Twitter, Instagram, Facebook } from '@mui/icons-material';



// Navigation links type
type NavLink = {
  name: string;
  section: string;
};

const navLinks: NavLink[] = [
  { name: 'About', section: 'about' },
  { name: 'Experience', section: 'experience' },
  { name: 'Projects', section: 'projects' },
  { name: 'Skills', section: 'skills' },
  { name: 'Contact', section: 'contact' },
];

const NavLink = styled('a')(({ theme }) => ({
  color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.text.primary,
  textDecoration: 'none',
  cursor: 'pointer',
  margin: theme.spacing(0, 2),
  fontWeight: 500,
  fontSize: '0.95rem',
  textTransform: 'uppercase',
  letterSpacing: '0.5px',
  padding: theme.spacing(1, 0),
  position: 'relative',
  transition: 'color 0.3s ease',
  '&:hover': {
    color: theme.palette.primary.main,
    '&::after': {
      transform: 'scaleX(1)',
      opacity: 1,
    },
  },
  '&::after': {
    content: '""',
    position: 'absolute',
    width: '100%',
    height: '2px',
    bottom: 0,
    left: 0,
    backgroundColor: theme.palette.primary.main,
    transform: 'scaleX(0)',
    transformOrigin: 'bottom right',
    transition: 'transform 0.3s ease, opacity 0.3s ease',
    opacity: 0,
  },
}));

// Social media brand colors
const socialColors: Record<string, string> = {
  'LinkedIn': '#0A66C2',
  'GitHub': '#181717',
  'Twitter': '#1DA1F2',
  'Instagram': 'transparent',
  'Facebook': '#1877F2',
};

// Map social media icons to their components and hover colors
const socialIcons: Record<string, { component: React.ComponentType; color?: string; gradient?: string }> = {
  'LinkedIn': { component: LinkedIn, color: socialColors.LinkedIn },
  'GitHub': { component: GitHub, color: socialColors.GitHub },
  'Twitter': { component: Twitter, color: socialColors.Twitter },
  'Instagram': { 
    component: Instagram, 
    color: 'transparent',
    gradient: 'linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%)' 
  },
  'Facebook': { component: Facebook, color: socialColors.Facebook },
};

interface HeaderProps {
  onNavClick: (section: string) => void;
}

const Header: React.FC<HeaderProps> = ({ onNavClick }) => {
  const theme = useTheme();
  const { mode, toggleColorMode } = useThemeContext();
  const { PersonalProfile, SocialProfile } = PersonalData;

  return (
    <AppBar 
      position="sticky" 
      elevation={0} 
      sx={{ 
        backdropFilter: 'blur(10px)', 
        backgroundColor: theme.palette.mode === 'dark' 
          ? 'rgba(18, 18, 18, 0.8)' 
          : 'rgba(255, 255, 255, 0.8)',
        borderBottom: `1px solid ${theme.palette.divider}`,
        transition: 'background-color 0.3s ease',
      }}
    >
      <Container maxWidth="lg">
        <Toolbar 
          disableGutters 
          sx={{ 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center',
            py: 1
          }}
        >
          {/* Left side - Name and Title */}
          <Box sx={{ display: 'flex', flexDirection: 'column', cursor: "pointer" }} onClick={() => onNavClick('home')}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                fontWeight: 700,
                background: theme.palette.mode === 'dark'
                  ? 'linear-gradient(45deg, #90caf9, #64b5f6)'
                  : 'linear-gradient(45deg, #1976d2, #2196f3)',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent',
                lineHeight: 1.2,
                letterSpacing: '0.5px',
              }}
            >
              {PersonalProfile.name}
            </Typography>
            <Typography 
              variant="subtitle2" 
              sx={{ 
                color: theme.palette.text.secondary,
                fontWeight: 500,
                fontSize: '0.75rem',
                lineHeight: 1.2,
              }}
            >
              {PersonalProfile.title}
            </Typography>
          </Box>


          {/* Navigation Links */}
          <Box sx={{ display: { xs: 'none', md: 'block' } }}>
            <Stack direction="row" spacing={3}>
              {navLinks.map((link) => (
                <NavLink
                  key={link.name}
                  href={`#${link.section}`}
                  onClick={(e: MouseEvent) => {
                    e.preventDefault();
                    onNavClick(link.section);
                  }}
                  sx={{
                    color: theme.palette.mode === 'dark' 
                      ? theme.palette.common.white 
                      : theme.palette.text.primary,
                    '&:hover': {
                      color: theme.palette.primary.main,
                    },
                  }}
                >
                  {link.name}
                </NavLink>
              ))}
            </Stack>
          </Box>

          {/* Right side - Social Icons and Theme Toggle */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            {/* Social Icons */}
            <Box sx={{ display: { xs: 'none', sm: 'flex' }, gap: 1, mr: 1 }}>
              {SocialProfile.slice(0, 4).map((social) => {
                const iconName = typeof social.icon === 'string' ? social.icon : '';
                const socialConfig = socialIcons[iconName];
                
                if (!socialConfig) return null;
                
                const { component: IconComponent, color, gradient } = socialConfig;
                const isInstagram = iconName === 'Instagram';
                
                return (
                  <Tooltip key={social.link} title={iconName} arrow>
                    <Box
                      component="a"
                      href={social.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      sx={{
                        display: 'inline-flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        width: 36,
                        height: 36,
                        color: theme.palette.mode === 'dark' 
                          ? theme.palette.common.white 
                          : theme.palette.text.primary,
                        position: 'relative',
                        overflow: 'hidden',
                        backgroundColor: 'transparent',
                        '&:hover': {
                          color: isInstagram ? '#fff' : '#fff',
                          transform: 'translateY(-2px)',
                          backgroundColor: isInstagram ? 'transparent' : color,
                          '&::before': {
                            opacity: isInstagram ? 1 : 0,
                          },
                          '& .MuiSvgIcon-root': {
                            position: 'relative',
                            zIndex: 1,
                          }
                        },
                        '&::before': {
                          content: '""',
                          position: 'absolute',
                          top: 0,
                          left: 0,
                          right: 0,
                          bottom: 0,
                          borderRadius: '50%',
                          background: isInstagram ? gradient : 'transparent',
                          opacity: 0,
                          transition: 'opacity 0.3s ease',
                        },
                        transition: 'all 0.3s ease',
                        borderRadius: '50%',
                        cursor: 'pointer',
                        textDecoration: 'none',
                      }}
                    >
                      <SvgIcon 
                        component={IconComponent}
                        sx={{ 
                          fontSize: '1.5rem',
                          color: isInstagram 
                            ? theme.palette.mode === 'dark' 
                              ? theme.palette.common.white 
                              : theme.palette.text.primary
                            : 'inherit',
                          transition: 'color 0.3s ease',
                        }} 
                      />
                    </Box>
                  </Tooltip>
                );
              })}
            </Box>
            
            {/* Theme Toggle */}
            <Tooltip title={`Switch to ${mode === 'dark' ? 'light' : 'dark'} mode`} arrow>
              <IconButton 
                onClick={toggleColorMode} 
                color="inherit"
                aria-label={mode === 'dark' ? 'Switch to light mode' : 'Switch to dark mode'}
                sx={{
                  color: theme.palette.mode === 'dark' 
                    ? theme.palette.common.white 
                    : theme.palette.text.primary,
                  '&:hover': {
                    backgroundColor: theme.palette.mode === 'dark' 
                      ? 'rgba(144, 202, 249, 0.1)' 
                      : 'rgba(25, 118, 210, 0.1)',
                    color: 'primary.main',
                  },
                }}
              >
                {mode === 'dark' ? 
                  <Brightness7Icon sx={{ color: 'inherit' }} /> : 
                  <Brightness4Icon sx={{ color: 'inherit' }} />
                }
              </IconButton>
            </Tooltip>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
