import emailjs, { EmailJSResponseStatus } from "@emailjs/browser";
import { PersonalData } from "./data/data";

export const sendEmail = (form: Record<string, unknown>): Promise<EmailJSResponseStatus> => {
    return emailjs.send(PersonalData.EmailJS.serviceID, PersonalData.EmailJS.templateID, form, PersonalData.EmailJS.publicKey)
}